
import {Component, Prop, Vue} from 'vue-property-decorator';

@Component
export default class NoteActions extends Vue {

    @Prop({default: () => true}) withUpdate !: boolean;
    @Prop({default: () => true}) withDelete !: boolean;


    private deleteClick() {
        this.$emit('deleteClick');
    }

    private editClick() {
        this.$emit('editClick');
    }
}
